import {Component, Input, Output, EventEmitter, HostBinding, ChangeDetectionStrategy} from '@angular/core';

@Component({
  selector: 'ngl-section',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <h3 class="slds-section__title"><button class="slds-button slds-section__title-action" type="button" (click)="toggle($event)"><svg class="slds-section__title-action-icon slds-button__icon slds-button__icon--left" nglIcon="switch"></svg>{{title}}</button></h3><div class="slds-section__content"><ng-content></ng-content></div>
  `,
  host: {
    '[class.slds-section]': 'true',
  },
})
export class NglSection {

  @Input() title: string;

  @HostBinding('class.slds-is-open')
  @Input() open = false;

  @Output() openChange = new EventEmitter<boolean>();

  toggle(event: Event) {
    event.preventDefault();
    this.openChange.emit(!this.open);
  }
}
